
.jp-userMent {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.jp-userMent .mui-scroll-wrapper {
  top: 3.78rem;
}
.jp-userMent .jp-screening {
  padding: 0.373333333333333rem;
  overflow: hidden;
  height: 2.64rem;
  border-bottom: 2px solid #f3f3f3;
  background-color: #fff;
}
.jp-userMent .jp-screening span {
  width: 2.906666666666667rem;
  height: 0.773333333333333rem;
  line-height: 0.773333333333333rem;
  font-size: 0.346666666666667rem;
  border-radius: 0.133333333333333rem;
  margin: 0.082666666666667rem;
  float: left;
  color: #f5ab18;
  border: 1px solid #f5ab18;
  text-align: center;
}
.jp-userMent .jp-screening span.active {
  background-color: #f5ab18;
  color: #fff;
}
.jp-userMent .jp-evaluation .listWrap {
  padding: 0.333333333333333rem 0.4rem;
}
.jp-userMent .jp-evaluation .listWrap border-bottom 2px solid #f3f3f3:last-child {
  border: 0;
}
.jp-userMent .jp-evaluation .listWrap .list-title {
  margin-bottom: 0.3rem;
}
.jp-userMent .jp-evaluation .listWrap .list-title .userName {
  font-size: 0.373333333333333rem;
  color: #4d4d4d;
  letter-spacing: 1px;
}
.jp-userMent .jp-evaluation .listWrap .list-title small {
  font-size: 0.293333333333333rem;
  color: #838383;
  margin-left: 0.3rem;
}
.jp-userMent .jp-evaluation .listWrap .list-title small.jp-time {
  float: right;
  margin-top: 0.18rem;
}
.jp-userMent .jp-evaluation .listWrap .listEva {
  font-size: 0.373333333333333rem;
  color: #4d4d4d;
  line-height: 0.5rem;
  margin-bottom: 0.3rem;
}
.jp-userMent .jp-evaluation .listWrap .listState {
  font-size: 0.373333333333333rem;
  color: #f54c18;
}


