
#aboutUs{
	width: 100%;
	height: 100%;
	background: white;
}
#aboutUs .topBar{
	width: 100%;
	height: 1.2rem;
	background-color: #F5AB18;
}
#header{
    box-shadow: none;
}
#aboutUs img{
	width: 100%;
}
#aboutUs .info{
	width: 92%;
	margin: 0.666666rem auto;
	font-size: 0.4rem;
	color: #5c5c5c;
	line-height: 0.666666rem
}
#aboutUs .info span{
	font-size: 0.533333rem
}
#aboutUs .appStatus{
	width: 92%;
	margin: 0.666666rem auto;
	font-size: 0.4rem;
	text-align: right;
	margin-top: 2.666666rem;
	color: #797979;
}



