
@import "../../../../static/css/muiConfirm.css";
/* 设置滚动条的样式 */
::-webkit-scrollbar {
  height: 0;
}
.productInfo {
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9;
}
.productInfo .jp-scroll {
  top: 1.133333333rem;
}
.productInfo .jp-scroll .tile {
  font-size: 0.453333333333333rem;
  padding: 0.373333333333333rem 0.373333333333333rem 0rem 0.373333333333333rem;
  background-color: #fff;
  color: #2a2a2a;
  text-indent: 0.45rem;
  position: relative;
}
.productInfo .jp-scroll .tile:before {
  position: absolute;
  content: " ";
  width: 25px;
  height: 25px;
  display: block;
  top: 55%;
  left: 4%;
  border-radius: 50%;
  background-color: #f9d58f;
}
.productInfo .jp-screening {
  height: 2.666666666666667rem;
  line-height: 2.666666666666667rem;
  padding: 0.4rem;
  overflow: hidden;
  border-bottom: 2px solid #f3f3f3;
}
.productInfo .jp-screening a {
  width: 2.88rem;
  height: 0.773333333333333rem;
  line-height: 0.773333333333333rem;
  font-size: 0.346666666666667rem;
  border-radius: 0.133333333333333rem;
  margin: 0.093333333333333rem;
  float: left;
  color: #f5ab18;
  border: 1px solid #f5ab18;
  text-align: center;
}
.productInfo .jp-screening a.router-link-active {
  background-color: #f5ab18;
  color: #fff;
}
.productInfo .jp-evaluation {
  min-height: 2.24rem;
  box-shadow: 0 15px 40px 0 rgba(0,0,0,0.1);
  margin-bottom: 0.0933rem;
}
.productInfo .jp-evaluation .listWrap {
  padding: 0.333333333333333rem 0.533333333333333rem;
  border-bottom: 2px solid #f3f3f3;
}
.productInfo .jp-evaluation .listWrap:last-child {
  border: 0;
}
.productInfo .jp-evaluation .listWrap .list-title {
  margin-bottom: 0.3rem;
}
.productInfo .jp-evaluation .listWrap .list-title .userName {
  font-size: 0.373333333333333rem;
  color: #4d4d4d;
  letter-spacing: 1px;
}
.productInfo .jp-evaluation .listWrap .list-title small {
  font-size: 0.293333333333333rem;
  color: #838383;
  margin-left: 0.3rem;
}
.productInfo .jp-evaluation .listWrap .list-title small.jp-time {
  float: right;
  margin-top: 0.15rem;
}
.productInfo .jp-evaluation .listWrap .listEva {
  font-size: 0.373333333333333rem;
  color: #4d4d4d;
  line-height: 0.5rem;
  margin-bottom: 0.3rem;
}
.productInfo .jp-evaluation .listWrap .listState {
  font-size: 0.373333333333333rem;
  color: #f54c18;
}
.productInfo .jp-evaluation .listWrap .view {
  text-align: center;
  padding: 0.333rem 0;
}
.productInfo .jp-evaluation .listWrap .view .viewAll {
  display: inline-block;
  text-align: center;
  width: 4rem;
  height: 0.866666666666667rem;
  line-height: 0.866666666666667rem;
  font-size: 0.4rem;
  border-radius: 0.133333333333333rem;
  color: #4d4d4d;
  background-color: #ffcaca;
}
.productInfo .conditions {
  padding: 0.4rem 0.4rem 0.4rem 0.866666666666667rem;
  font-size: 0.4rem;
  line-height: 0.6rem;
  color: #4d4d4d;
  box-shadow: 0 15px 40px 0 rgba(0,0,0,0.1);
  margin-bottom: 0.0933rem;
}
.productInfo .conditions p {
  font-size: 0.4rem;
  line-height: 0.4rem;
  color: #4d4d4d;
}
.productInfo .processWrap {
  overflow: hidden;
}
.productInfo .processWrap .process {
  min-width: 100%;
  white-space: nowrap;
  float: left;
  padding: 0.4rem 0.32rem 0.293333333333333rem 0.866666666666667rem;
  margin-bottom: 0.1333rem;
  box-shadow: 0 15px 40px 0 rgba(0,0,0,0.1);
}
.productInfo .processWrap .process li {
  font-size: 0.293333333333333rem;
  width: 2.4rem;
  display: inline-block;
  position: relative;
  background-repeat: no-repeat;
  background-size: 40%, 10%;
  height: 1.8rem;
  text-indent: 0.08rem;
  line-height: 2.8rem;
  color: #5197e9;
}
.productInfo .processWrap .process li.process1 {
  background-image: url("cpxq_gzxx.png"), url("cpxq_icom_sqlc.png");
  background-position: 15% 0, 80% 34%;
}
.productInfo .processWrap .process li.process2 {
  background-image: url("cpxq_gzxx.png"), url("cpxq_icom_sqlc.png");
  background-position: 15% 0, 80% 34%;
}
.productInfo .processWrap .process li.process3 {
  background-image: url("cpxq_sjrz.png"), url("cpxq_icom_sqlc.png");
  background-position: 15% 0, 80% 34%;
}
.productInfo .processWrap .process li.process4 {
  background-image: url("cpxq_yhkrz.png"), url("cpxq_icom_sqlc.png");
  background-position: 17% 0, 80% 34%;
}
.productInfo .processWrap .process li.process5 {
  background-image: url("cpxq_zmxy.png"), url("cpxq_icom_sqlc.png");
  background-position: center 0;
}
.productInfo .processWrap .process li.process6 {
  background-image: url("cpxq_xxrxx.png"), url("cpxq_icom_sqlc.png");
  background-position: 20% 0, 80% 34%;
}
.productInfo .processWrap .process li.process7 {
  background-image: url("cpxq_yxs.png"), url("cpxq_icom_sqlc.png");
  background-position: 20% 0, 80% 34%;
}
.productInfo .processWrap .process li.process8 {
  background-image: url("cpxq_sfrz.png"), url("cpxq_icom_sqlc.png");
  background-position: 13% 0, 80% 34%;
}
.productInfo .processWrap .process li.process9 {
  background-image: url("cpxq_zxbg.png"), url("cpxq_icom_sqlc.png");
  background-position: 13% 0, 80% 34%;
}
.productInfo .processWrap .process li.process10 {
  background-image: url("cpxq_rlsb.png"), url("cpxq_icom_sqlc.png");
  background-position: 13% 0, 80% 34%;
}
.productInfo .processWrap .process li.process11 {
  background-image: url("cpxq_dsrz.png"), url("cpxq_icom_sqlc.png");
  background-position: 13% 0, 80% 34%;
}
.productInfo .processWrap .process li.process12 {
  background-image: url("cpxq_txsq.png"), url("cpxq_icom_sqlc.png");
  background-position: 13% 0, 80% 34%;
}
.productInfo .processWrap .process li.process13 {
  background-image: url("cpxq_sprz.png"), url("cpxq_icom_sqlc.png");
  background-position: 13% 0, 80% 34%;
}
.productInfo .processWrap .process li:last-child:before {
  position: absolute;
  content: " ";
  width: 30px;
  height: 30px;
  display: block;
  top: 30%;
  right: 15%;
  background-color: #fff;
}
.productInfo .navBottom {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  z-index: 99;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-top: 1px solid #ccc;
  font-size: 0.4rem;
}
.productInfo .navBottom a,
.productInfo .navBottom div {
  height: 1.306666666666667rem;
  line-height: 1.306666666666667rem;
  text-align: center;
  background-color: #fff;
  color: #1f1f1f;
}
.productInfo .navBottom a.eva,
.productInfo .navBottom div.eva,
.productInfo .navBottom a.strategy,
.productInfo .navBottom div.strategy {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 2rem;
  background-repeat: no-repeat;
}
.productInfo .navBottom a.eva,
.productInfo .navBottom div.eva {
  background-image: url("icom_pj.png");
  background-size: 20%;
  background-position: center 10%;
}
.productInfo .navBottom a.strategy,
.productInfo .navBottom div.strategy {
  background-image: url("icom_gl.png");
  background-size: 20%;
  background-position: center 15%;
}
.productInfo .navBottom a.please,
.productInfo .navBottom div.please {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 3.866rem;
          flex: 0 0 3.866rem;
  background-color: #f57963;
  color: #fff;
}
#mask {
  width: 100%;
  height: 100%;
  background-color: #fff;
  position: fixed;
  z-index: 8;
}


