
#help{
	width: 100%;
	height: 100%;
	background: white;
}
#help .topBar{
	width: 100%;
	height: 1.2rem;
	background-color: #F5AB18;
}
#header{
    box-shadow: none;
}
#help .helpList{
	width: 100%;
	background-color: #f3f3f3;
	list-style-position:inside;
	color: #5c5c5c;
}
#help .helpList li{
	width: 100%;
	padding: 0 4%;
	height: 1.333333rem;
	line-height: 1.333333rem;
	font-size: 0.4rem;
	border-bottom: 1px solid #e3e3e3;
	background: white;
	list-style: decimal inside;
}
#help .helpList li span{
	vertical-align: baseline;
}
#help .helpList li img{
	float: right;
	margin-top: 0.533333rem;
}
#help .helpList .detail{
	width: 100%;
	padding: 0 4%;
	font-size: 0.32rem;
	line-height: 0.4rem;
}



