
.addMent .jp-scroll {
  position: static;
  z-index: 99;
}
.addMent .title {
  font-size: 0.4rem;
  padding: 0.4rem 0rem;
  color: #000;
  text-align: center;
}
.addMent .state {
  overflow: hidden;
}
.addMent .state li {
  width: 1.813333333333333rem;
  height: 0.666666666666667rem;
  line-height: 0.666666666666667rem;
  font-size: 0.346666666666667rem;
  margin: 0.133333333333333rem 0.733333333333333rem 0.506666666666667rem 0.733333333333333rem;
  float: left;
  text-align: center;
  color: #9a9a9a;
  border: 2px solid #9a9a9a;
}
.addMent .state li.active {
  border-color: #fc4a28;
  color: #fc4a28;
}
.addMent .addStars {
  text-align: center;
  padding: 0.4rem 0rem;
  font-size: 0.4rem;
}
.addMent .addStars .text {
  vertical-align: text-top;
}
.addMent .option {
  border: 0.16rem solid #f3f3f3;
}
.addMent .option .options {
  padding: 0.226666666666667rem;
  overflow: hidden;
  border-radius: 3px;
  box-shadow: 0 3px 8px 3px rgba(0,0,0,0.2);
}
.addMent .option .options div {
  width: 2.906666666666667rem;
  height: 0.773333333333333rem;
  line-height: 0.773333333333333rem;
  font-size: 0.346666666666667rem;
  border-radius: 0.133333333333333rem;
  margin: 0.082666666666667rem;
  float: left;
  color: #f5ab18;
  border: 1px solid #f5ab18;
  text-align: center;
}
.addMent .option .options div.active {
  background-color: #f5ab18;
  color: #fff;
}
.addMent .addArea {
  font-size: 0.426666666666667rem;
  padding: 0.4rem;
  line-height: 0.5rem;
  border: 0;
}
.addMent .addSub {
  font-size: 0.533333333333333rem;
  height: 1.653333333333333rem;
  line-height: 1.653333333333333rem;
  width: 100%;
  text-align: center;
  color: #fff;
  background-color: #f57963;
  position: fixed;
  bottom: 0;
  z-index: 0;
}


