
#login{
		width: 100%;
		height: 100%;
		background: white;
		padding-top: 1.066666rem;
}
#login .jp-topBack #header{
		background: white;
		border-bottom: 1px solid #ccc;
}
#login .jp-topBack #header h1{
		color: black;
		font-size: 0.4rem;
		font-weight: bold;
}
#login .userPhone,.smsNumber{
		width: 100%;
		height: 1.6rem;
		border-bottom: 1px solid #ccc;
		line-height: 1.6rem;
		font-size: 0.533333rem;
}
#login span{
		margin: 0 0.266666rem;
		color: #ccc;
}
#login img{
		margin-left: 0.64rem;
		width: 0.666666rem;
		height: 0.666666rem;
		vertical-align: text-top;
}
#login>div input{
		width: 70%;
		border: none;
		height:0.8rem;
		vertical-align: baseline;
		line-height: 0.8rem;
		font-size: 0.4rem;
}
#login .loginBtn{
		width: 92%;
		height: 1.066666rem;
		border-radius: 0.266666rem;
		margin: 1.6rem auto 0.826666rem auto;
		text-align: center;
		line-height: 1.066666rem;
		font-size: 0.4rem;
		color: white;
		background: #F5AB18;
}
#login #header a{
		color: #000000;
}
#app #login .jp-topBack #header .mui-title{
		font-size: 0.533333rem;
}
#login .tips{
		width: 92%;
		height: 1rem;
		line-height: 1rem;
		margin: 0 auto;
		display:-webkit-box;
		display:-ms-flexbox;
		display:flex;
}
#login .tips a:nth-child(1){
		color: #7c7c7c;
		font-size: 0.4rem;
}
#login .tips a:nth-child(2){
		color: #f57018;
		font-size: 0.4rem;
		margin-left: 37%;
}
#login .tips .loginKey span{
    color: #f57018;
    margin:0;
}



