
#jp-list {
  padding: 0 0.1333rem;
}
#jp-list .listWrap {
  height: 3.0666rem;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 0.1333rem;
  overflow: hidden;
}
#jp-list .listWrap .wrapCotnet {
  width: 100%;
  height: 2.4rem;
  padding: 0.333rem 0.24rem;
}
#jp-list .listWrap .wrapCotnet .wrap-a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}
#jp-list .listWrap .wrapCotnet .wrap-a .wrapLeft {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#jp-list .listWrap .wrapCotnet .wrap-a .wrapLeft img {
  width: 1.68rem;
  height: 1.68rem;
  float: left;
  margin-right: 0.4rem;
  border-radius: 50%;
}
#jp-list .listWrap .wrapCotnet .wrap-a .wrapLeft .username {
  width: 100%;
}
#jp-list .listWrap .wrapCotnet .wrap-a .wrapLeft .username h3 {
  font-size: 0.4rem;
  line-height: 0.555rem;
  color: #1d1d1d;
}
#jp-list .listWrap .wrapCotnet .wrap-a .wrapLeft .username h4 {
  font-size: 0.2933rem;
  line-height: 0.5rem;
  color: #959595;
}
#jp-list .listWrap .wrapCotnet .wrap-a .wrapRight {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 3.4rem;
          flex: 0 0 3.4rem;
}
#jp-list .listWrap .wrapCotnet .wrap-a .wrapRight h4 {
  font-size: 0.2933rem;
  line-height: 0.45rem;
  margin-bottom: 0.2rem;
  color: #959595;
}
#jp-list .listWrap .wrapCotnet .wrap-a .wrapRight .btm-anniu {
  width: 100%;
  height: 1rem;
  background-image: url("anniu_kded.png");
  background-size: contain;
  background-repeat: no-repeat;
  font-size: 0.32rem;
  line-height: 0.95rem;
  text-indent: 1.1rem;
  color: #e83428;
}
#jp-list .listWrap .wrapCotnet .wrap-a .wrapRight .btm-anniu span {
  font-size: 120%;
}
#jp-list .listWrap .wrapCotnet .wrap-a .wrapRight .btm-anniu.active {
  background-image: url("anniu_pgz.png");
  color: #706e6b;
}
#jp-list .listWrap .wrapCotnet .wrap-a .wrapRight .btm-anniu.active span {
  font-size: 130%;
}
#jp-list .listWrap .listNotice {
  width: 100%;
  height: 0.666rem;
  border-top: 0.013333rem solid #f3f3f3;
}
#jp-list .listWrap .listNotice .icon-notice {
  width: 0.426666666666667rem;
  height: 0.426666666666667rem;
  line-height: 0.426666666666667rem;
  display: inline-block;
  background-image: url("icom_gb.png");
  background-repeat: no-repeat;
  background-size: contain;
  margin: 0.133333333333333rem 0.293333333333333rem;
}
#jp-list .listWrap .listNotice span {
  font-size: 0.293333333333333rem;
  color: #747474;
  display: inline-block;
  vertical-align: top;
  height: 0.733333333333333rem;
  line-height: 0.733333333333333rem;
}


