
#findPwd{
		width: 100%;
		height: 100%;
		background: white;
		padding-top: 1.066666rem;
}
#findPwd .jp-topBack #header{
		background: white;
		border-bottom: 1px solid #ccc;
}
#findPwd .jp-topBack #header h1{
		color: black;
		font-size: 0.4rem;
		font-weight: bold;
}
#findPwd .userPhone,.smsNumber{
		width: 100%;
		height: 1.6rem;
		border-bottom: 1px solid #ccc;
		line-height: 1.6rem;
		font-size: 0.533333rem;
}
#findPwd span{
		margin: 0 0.266666rem;
		color: #ccc;
}
#findPwd img{
		margin-left: 0.64rem;
		width: 0.666666rem;
		height: 0.666666rem;
		vertical-align: text-top;
}
#findPwd .userPhone input{
		width: 70%;
		border: none;
		height:0.8rem;
		vertical-align: baseline;
		line-height: 0.8rem;
		font-size: 0.4rem;
}
#findPwd .smsNumber input{
		width: 48%;
		border: none;
		height:0.8rem;
		vertical-align: baseline;
		line-height: 0.8rem;
		font-size: 0.4rem;
}
#findPwd .smsNumber .getMsgNumber{
		display: inline-block;
		width: 2.666666rem;
		height: 1.066666rem;
		background-color: #F5AB18;
    	border:0;
		text-align: center;
    	vertical-align: middle;
		font-size: 0.4rem;
		color: white;
		border-radius: 0.266666rem;
}
#findPwd .loginBtn{
		width: 92%;
		height: 1.066666rem;
		border-radius: 0.266666rem;
		margin: 1.6rem auto 0.826666rem auto;
		text-align: center;
		line-height: 1.066666rem;
		font-size: 0.4rem;
		color: white;
		background: #F5AB18;
}
#findPwd #header a{
		color: #000000;
}
#app #findPwd .jp-topBack #header .mui-title{
		font-size: 0.533333rem;
}
#findPwd .loginKey{
		color: #7c7c7c;
		font-size: 0.4rem;
		margin-left: 0.4rem;
}



