
.jp-topBack iframe {
  padding-top: 1.2rem;
}
.jp-topBack #header {
  background-color: #f5ab18;
  box-shadow: none;
  height: 1.133333333333333rem;
  line-height: 1.133333333333333rem;
  padding: 0.293333333333333rem 0.466666666666667rem;
  box-sizing: border-box;
}
.jp-topBack #header a,
.jp-topBack #header .mui-title {
  color: #fff;
  font-size: 0.426666666666667rem;
}
.jp-topBack #header a {
  padding: 0;
  font-size: 0.64rem;
}
.jp-topBack #header .mui-title {
  height: 0.6rem;
  line-height: 0.6rem;
  letter-spacing: 2px;
}
.jp-topBack #header .mui-pull-right {
  margin-right: -20px;
}
.jp-topBack #header .jp-left40 {
  right: 1.2rem;
}
.jp-topBack #header .jp-icon-collection,
.jp-topBack #header .jp-icon-share {
  height: 0.586666666666667rem;
  line-height: 0.586666666666667rem;
  width: 0.586666666666667rem;
  background-repeat: no-repeat;
  background-size: contain;
}


