
#userStatus{
  width: 100%;
  height: 100%;
  background: white;
}
#userStatus .topBar{
  width: 100%;
  height: 1.2rem;
  background-color: #F5AB18;
}
#header{
  box-shadow: none;
}
#userStatus .wrap{
  width: 92%;
  margin: 0 auto;
  list-style: none;
}
#userStatus .wrap li{
  width: 100%;
  height: 1.333333rem;
  border-bottom: 1px solid #dcdcdc;
  line-height: 1.333333rem;
  font-size: 0.4rem;
  color: #5c5c5c;
}
#userStatus .wrap li .input{
  width: 70%;
  font-size:0.346666rem;
  text-align: right;
  float: right;
  border: none;
  outline: none;
  margin-top: 0.4rem;
  height: 0.533333rem;
  line-height: 0.533333rem;
}
#userStatus .wrap .married .radioWrap{
  float: right;
  height: 1.333333rem;
}
#userStatus .wrap .married .radioWrap div{
  line-height: 0.8;
  display: inline-block;
}
#userStatus .wrap .married .xu_lable{
  padding: 0;
  margin-left: 0.8rem;
}
#userStatus .xu_save{
  width: 100%;
  height: 1.333333rem;
  text-align: center;
  line-height: 1.333333rem;
  color: white;
  background-color: #fa7a2d;
  font-size: 0.533333rem;
  position: fixed;
  bottom: 0;
  left: 0;
}



