
@import url("../../../static/css/muiConfirm.css");
#creditinfo{
		width: 100%;
		height: 100%;
		background: white;
}
#creditinfo .topBar{
		width: 100%;
		height: 1.2rem;
		background-color: #F5AB18;
}
#creditinfo .selectBar a{
		display:block;
		width: 30%;
		height: 1.2rem;
		margin: 0 1.666666666%;
		float: left;
}
#header{
	    box-shadow: none;
}
#creditinfo .wrap{
		width: 92%;
		margin: 0 auto;
		list-style: none;
}
#creditinfo .wrap li{
		width: 100%;
		height: 1.333333rem;
		border-bottom: 1px solid #dcdcdc;
		line-height: 1.333333rem;
		font-size: 0.4rem;
		color: #5c5c5c;
}
#creditinfo .wrap li .input{
		width: 70%;
		font-size:0.346666rem;
		text-align: right;
		float: right;
		border: none;
		outline: none;
		margin-top: 0.4rem;
		height: 0.65rem;
		line-height: 0.65rem;
		padding-right: 0;
}
#creditinfo .wrap .married .radioWrap{
		float: right;
		height: 1.333333rem;
}
#creditinfo .wrap .married .radioWrap div{
		line-height: 0.8;
		display: inline-block;
}
#creditinfo .wrap .married .marry_lable{
		padding: 0;
		margin-left: 0.8rem;
}
#creditinfo .wrap .selection .radioWrap{
	float: right;
	height: 1.333333rem;
}
#creditinfo .wrap .selection .radioWrap div{
		line-height: 0.8;
		display: inline-block;
}
#creditinfo .wrap .selection .xu_lable{
		padding: 0;
		margin-left: 0.8rem;
}
#creditinfo .saveWrap{
		width:100% ;
		height:2.3rem;
		background: white;
}
#creditinfo .save{
		width: 2.693333rem;
		height: 1.333333rem;
		background-color: #f5ab18;
		font-size: 0.4rem;
		line-height: 1.333333rem;
		text-align: center;
		color: white;
		border-radius: 0.2rem;
		margin: 0.666666rem auto;
}
	


