
@import '../../../static/css/swiper-3.4.2.min.css';
@import url("../../../static/css/muiConfirm.css");
#home{
	padding-top:1.2rem;
	width: 100%;
	height: 100%;
	background: #fff;
	position: relative;
}
#home .shenqing{
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.7);
	position: fixed;
	z-index: 99999;
	top: 0;
	left: 0;
}
#home .fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
#home .fade-enter, .fade-leave-active {
  opacity: 0
}
#home .shenqing>div{
	width: 7.546666rem;
	height: 8.693333rem;
	position: absolute;
	top: 0;
	bottom:0;
	left: 0;
	right: 0;
	margin: auto;
}
#home .shenqing>div>img:nth-child(1){
	position: absolute;
	top:0.266666rem;
	right: 0.266666rem;
	width: 0.533333rem;
	height: 0.533333rem;
}
#home .shenqing>div img:nth-child(2){
	width: 100%;
	height: 100%;
}
#home .shenqing>div .jump{
	width: 80%;
	height: 1.333333rem;
	position: absolute;
	left: 10%;
	bottom: 3%;
	background-color: transparent;
}
#home .topBar{
	width: 100%;
	height: 1.2rem;
	background-color: #f5ab18;
	line-height: 1.2rem;
	font-size: 0.506666rem;
	color: white;
	text-align: center;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 10;
}
#home .topBar a{
	font-size: 0.346666rem;
	width: 4em;
	height: auto;
	position: absolute;
	top: auto;
	left: 0.2rem;
	color: white;
	text-decoration: none;
}
#home .homeWrap{
	height: 100%;
}
#home .homeWrap .smallIcon{
	position: fixed;
	width: 2.333333rem;
	height: 1.933333rem;
	right: 0;
	bottom:30%;
}
#home .homeWrap .smallIcon img{
	width: 2.333333rem;
	height: 1.933333rem;
}
#home .homeWrap .tips{
	font-size: 0.32rem;
	height: 2em;
	line-height: 2em;
	position: absolute;
	top: 1.44rem;
	right: 0.4rem;
}
#home .homeWrap .tips span{
	margin: 0 0.066666rem;
}
#home .swiper-container_home{
	border-bottom: 0.026666rem solid #f3f3f3;
	/*border-bottom: 0.026666rem solid #f3f3f3;*/
	position: relative;
}
#home .swiper-container_home .swiper-wrapper_home{
	height: 1.733333rem;
	width: 9.2rem;
	padding: 0 0.4rem;
}
#home .swiper-container_home .swiper-slide_home{
	height: 1.733333rem;
	line-height: 1.733333rem;
}
#home .swiper-slide_home a{
	display: inline-block;
	width: 100%;
	height: 100%;
}
#home .swiper-slide_home .lunboImg{
	width:1.413333rem ;
	height: 1.413333rem;
	border-radius: 50%;
	margin-top: 0.16rem;
}
#home .swiper-slide_home .lunboDetail{
	float: right;
	width: 80%;
	height: 100%;
	line-height:0.55rem;
}
#home .swiper-slide_home .lunboDetail .lunboTitle{
	font-size: 0.4rem;
	color:#1d1d1d;
	margin-top: 0.32rem;
	font-weight: bold;
}
#home .swiper-slide_home .lunboDetail .detail{
	font-size: 0.32rem;
	color:#727272;
}
#home .swiper-slide_home .lunboDetail .detail span{
	color: #ff3b3b;
}
#home .homeWrap .list{
	overflow: scroll;
	padding-bottom: 2rem;
}
#home .homeWrap .list .tempWrap{
	width: 100%;
	padding: 0 0.4rem;
}
#home .homeWrap .list .tempWrap>div{
	width: 48%;
	padding-top: 0.4rem;
}
#home .homeWrap .list .listLeft{
	float: left;
}
#home .homeWrap .list .listRight{
	float: right;
}
#home .homeWrap .list .listTemp{
	width: 100%;
	height: 2.826666rem;
	background-color: #f57b18;
	margin-bottom: 0.133333rem;
	border-radius: 0.266666rem;
	text-align: center;
}
#home .homeWrap .list .blue{
	background: #f59f18;
}
#home .homeWrap .list .listTemp a{
	color: white;
	display: inline-block;
}
#home .homeWrap .list .listTemp a img{
	width: 0.906666rem;
	height: 0.906666rem;
}
#home .homeWrap .list .listTemp a p{
	color: white;
	line-height: 0.533333rem;
	font-size: 0.4rem;
}
#home .homeWrap .list .listTemp a p span{
	font-weight: bold;
}
#home .homeWrap .list .listTemp a p:nth-child(1){
	margin-top: 0.4rem;
}
#home .homeWrap .list .listTemp a p:nth-child(2){
	color: white;
	line-height: 0.4rem;
	font-size: 0.32rem;
}



