
@import url("../../../static/css/muiConfirm.css");
@import url("../../style/icon-font/iconfont.css");
#personal {
    width: 100%;
    height: 100%;
    background: white;
}
#personal .userLogo {
    width: 100%;
    height: 5.84rem;
    background: #f5ab18;
    position: relative;
    text-align: center;
}
#personal .userLogo .img {
    width: 2.8rem;
    height: 2.8rem;
    border-radius: 50%;
    margin-top: 1.32rem;
}
#personal .userLogo .userId {
    font-size: 0.533333rem;
    color: white;
    margin-top: 0.346666rem;
}
#personal .userAbout {
    width: 100%;
    margin: 0 auto;
    list-style: none;
}
#personal .userAbout > a {
    display: block;
}
#personal .userAbout li {
    width: 100%;
    height: 1.333333rem;
    border-bottom: 2px solid #e3e3e3;
    line-height: 1.333333rem;
}
#personal .exit {
    width: 100%;
    height: 4rem;
    padding-top: 1.5rem;
}
#personal .exit>div{
  	width: 7.413333rem;
  	height: 1.333333rem;
    color: #4d4d4d;
    font-size: 0.4rem;
    text-align: center;
    line-height: 1.333333rem;
    margin:auto;
    background-color: #F5c764;
    border-radius: 0.133333rem;
}
#personal .userAbout li > span {
    color: #5c5c5c;
    font-size: 0.4rem;
    margin-left: 0.56rem;
}
#personal .userAbout li > span>i{
		display: inline-block;
		font-size: 0.4rem;
		color: #5c5c5c;
		margin-right: 0.3rem;
		font-weight: bold;
}
#personal .userAbout li > img {
    width: 0.186666rem;
    height: 0.32rem;
   	float: right;
   	margin-right: 0.4rem;
   	margin-top: 0.5rem;
}
#personal .addHei{
  	width:100%;
  	height: 1.2rem;
  	background-color: white;
}



