
.product{
  width: 100%;
  height: 100%;
  background: #f3f3f3;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 5;
}
.product .topBar{
  width: 100%;
  height: 1.2rem;
  background-color: #f5ab18;
  line-height: 1.2rem;
  text-align: center;
  position: fixed;
  z-index: 9999;
}
.product .topBar p{
  font-size: 0.533333rem;
  color: white;
}
.product .selectBar{
  width:100%;
  height:1.12rem ;
  background-color: white;
  position: fixed;
  z-index: 9999;
  margin-top: 1.2rem;
}
.product .selectBar li{
  width: 30%;
  height: 100%;
  float: left;
  margin:0 1.6%;
  line-height: 1.12rem;
  font-size:0.373333rem;
  color: #3d3d3d;
  text-align: center;
}
.product .selectBar li img{
  margin-left: 0.133333rem;
  width: 0.266666rem;
  height: 0.16rem;
}
.product .selectTab{
  width: 100%;
  background-color: #f5f5f5;
  position: fixed;
  top: 2.32rem;
  left: 0;
  z-index: 99;
  border-top: 2px solid #BBBBBB;
}
.product .selectTab .sfTab{
  width: 100%;
  padding-top: 0.4rem;
  position: relative;
  z-index: 9999;
  background: white;
}
.product .selectTab .sfTab span{
  display: inline-block;
  width: 20%;
  height: 0.746666rem;
  background: #dcdcdc;
  margin: 0 2.22222222222% 0.293333rem ;
  text-align: center;
  line-height: 0.7466666rem;
  font-size: 0.32rem;
  border-radius: 0.133333rem;
  color: #6d6868;
}
.product .selectTab .lxTab{
  width: 100%;
  text-align: center;
  position: relative;
  z-index: 9999;
  background: white;
}
.product .selectTab .lxTab>div{
  text-align: left;
}
.product .selectTab .sfTab .active{
  color: white;
  background: #f5ab18;
}
.product .selectTab .lxTab .active{
  color: white;
  background: #f5ab18;
}
.product .selectTab .lxTab  p{
  font-size: 0.4rem;
  color: #3D3D3D;
  line-height: 1.066666rem;
  margin-left: 0.4rem;
}
.product .selectTab .lxTab  span{
  display: inline-block;
  width: 20%;
  height: 0.746666rem;
  background: white;
  margin: 0 2.22222222222% 0.293333rem ;
  text-align: center;
  line-height: 0.7466666rem;
  font-size: 0.32rem;
  border-radius: 0.133333rem;
  color: #F5AB18;
  border:0.026666rem solid #F5AB18;
}
.product .selectTab .lxTab button{
  width: 2.4rem;
  height: 0.773333rem;
  font-size: 0.32rem;
  background: #f94848;
  color: white;
  margin: 0.4rem 0.8rem;
  border: none;
  border-radius: 0.133333rem;
}
.product .selectTab .lxTab button.jp-reset{
  color: #3d3d3d;
  background-color:#d9d9d9 ;
}
.product .warnWrap{
	width: 4rem;
	height: 5rem;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	margin:auto;
	z-index: 98;
}
.product .warnWrap img{
	width: 100%;
}
.product .warnWrap p{
	font-size: 0.426666rem;
	line-height: 0.8rem;
	text-align: center;
}



