
.jp-stars {
  display: inline-block;
  vertical-align: sub;
}
.jp-stars .star-item {
  margin: 0rem 0.106666666666667rem;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  line-height: 0.4rem;
}
.jp-stars .star-item:first-child {
  margin-left: 0.3rem;
}
.jp-stars .star-item.pLg {
  padding: 0.32rem;
  margin: 0 0.32rem;
}
.jp-stars .star-item.jp-on {
  background-image: url("tjpj_icom_cpmyd_ct.png");
}
.jp-stars .star-item.jp-yes {
  background-image: url("tjpj_icom_cpmyd_xz.png");
}


